import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Modal } from 'antd';
import { CartHeadAction } from 'components/common';
import FormSearch from './FormSearch';
import DataSheet from './DataSheet';
import FormCreateNew from './FormCreateNew';
import NotePlan from './NotePlan';

class WholeSchoolTrainingPlanPage extends Component {
  state = { visible: false };
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <Card title="Kế hoạch đào tạo toàn trường">
        <CartHeadAction>
          <BtnAddNew onClick={this.showModal} />
        </CartHeadAction>
        <FormSearch />
        <DataSheet />
        <NotePlan />
        {/* <TimelineDemo /> */}
        <Modal
          title="Tạo kế hoạch mới"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <FormCreateNew />
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <WholeSchoolTrainingPlanPage />
  </Page>
);



